* {
  box-sizing: border-box;
  font-family: "Beleren Bold";
}

a {
  text-decoration: none;
}

.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  grid-gap: 30px;
  gap: 30px;
  margin-top: 30px;
}

.parent {
  display: flex;
  flex-direction: column;
  grid-gap: 5px;
  gap: 5px;
}

.parent:hover .textOverlay {
  opacity: 1;
}

.walkerWrapper {
  overflow: hidden;
  position: relative;
  text-align: center;
  height: 300px;
  width: 250px;
  border-radius: 15px;
}

.walkerImage {
  object-fit: cover;
  height: 300px;
  width: 250px;
}

.textOverlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: 0.5s ease;
}

.description {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: white;
  background-color: grey;
}

.name {
  text-align: center;
}

.Desparked {
  background: red;
}

.Desparked img {
  -webkit-filter: grayscale(100%);
  filter: grayscale(100%);
  opacity: 0.5;
}

.Unknown {
  -webkit-filter: saturate(0);
          filter: saturate(0);
  opacity: 0.3;
}

